.tabs {
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  width: auto;
  /* padding: 24px 32px; */
  border: 1px solid #f1f1f1;
  background-color: #fff;
  box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.1);
  /* margin: 80px 0; */;
}

.tab-list {
  display: flex;
  padding-bottom: 24px;
}

.tab-progress {
  position: absolute;
  left: -1px;
  right: -1px;
  top: 66px;
  height: 4px;
  z-index: 0;
  background-color: #c000ff!important;
  transform-origin: 0%;
}

.tab {
  outline: none;
  cursor: pointer;
  border: none;
  font-size: 16px;
  line-height: 24px;
  padding: 8px 16px;
  color: #c000ff!important;
  background-color: #fff;
  border: 1px solid #f1f1f1;
  box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.1);
  /* margin-right: 24px; */
  transition: color 0.16s ease-in-out, background-color 0.16s ease-in-out,
    border-color 0.16s ease-in-out;
}

.tab:focus,
.tab:focus-visible {
  box-shadow: 0 0 0 4px rgba(73, 39, 150, 0.5);
}

.tab:focus:not(:focus-visible) {
  box-shadow: none;
}

.tab.active {
  border-color: #c000ff!important;
  color: white;
  border-radius: 7px;
  background-color: #161415;
  cursor: default;
}

.panel-list {
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;
}

.panel {
  position: absolute;
}

@media only screen and (max-width: 375px) {
  .tab {
  /* outline: none; */
    cursor: pointer;
    border: none;
    font-size: 16px;
    line-height: 24px;
    padding: 8px 6px;
    color: #c000ff!important;
    background-color: #fff;
    border: 1px solid #f1f1f1;
    box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.1);
  /* margin-right: 24px; */
    transition: color 0.16s ease-in-out, background-color 0.16s ease-in-out, border-color 0.16s ease-in-out;
  }
}